.button {
    cursor: pointer;
    font-weight: 500;
    font-size: 18px;
    line-height: 25px;
    color: $white;
    background: $orange;
    padding: 18px 36.5px;
    &:hover {
        background: $orange2;
    }
    &.button-form {
        padding: 20px 37px;
    }
    &.button-destinations {
        padding: 20px 34px;
    }
}